.form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: flex-start;
  justify-content: center;
}

.form__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.form > * {
  margin-left: 3rem;
}

.time {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
}

.submit {
  padding: 1rem 3rem;
  border-radius: 3rem;
  border: none;
  background-color: #2563eb;
  color: white;
}
