.log__item {
  display: grid;
  grid-template-columns: 16rem 16rem;
  grid-template-rows: 1fr 1fr;
  grid-auto-rows: auto;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to top left, #2563eb, #60a5fa);
  color: #dbeafe;
  padding: 1rem;
  border-radius: 1rem;
}

.log__group {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.log__group > p {
  font-size: 1.2rem;
  margin-left: 2rem;
  color: white;
}
@media (max-width: 680px) {
  .log__group > p {
    font-size: 1.5rem;
  }
}
